import { ref, defineComponent } from 'vue';
export default defineComponent({
  props: {
    confirmText: String
  },
  emits: ['confirmSubmit', 'update:modelValue', 'closedModel'],

  setup(props, {
    emit
  }) {
    const drawerDOM = ref();

    const closeDrawer = () => drawerDOM.value.handleClose();

    const closedDrawer = () => emit('closedModel', false);

    return {
      drawerDOM,
      closeDrawer,
      closedDrawer
    };
  }

});