import TemplateFormItem from './templateIFormtem.vue';
import TemplateBtnItem from './templateFormBtn.vue';
import { ref, watch, defineComponent, toRefs, toRaw } from 'vue';
import { deepClone } from '@/utils/toolFn';
export default defineComponent({
  props: {
    options: {
      type: Object,
      require: true
    },
    formInfo: {
      type: Object,
      require: true
    }
  },
  components: {
    TemplateFormItem,
    TemplateBtnItem
  },
  emits: ['submit', 'reset'],

  setup(props, {
    emit
  }) {
    let formData = ref({}),
        formOptions = ref({}),
        templateForm = ref(),
        {
      options,
      formInfo
    } = toRefs(props),
        defaultFormOptions = {
      inline: true,
      labelWidth: "100px",
      size: "default"
    }; // 按钮点击快捷处理

    const clickCallback = (eventName, formEl) => {
      if (eventName && eventName === 'submit' || eventName === 'reset') {
        if (eventName === 'submit' && formEl) {
          validate(() => emit('submit', deepClone(toRaw(formData.value))));
        }

        eventName === 'reset' && resetFields();
      }
    }; // 验证表单规则并且提供父组件调用


    const validate = fn => templateForm.value && templateForm.value.validate(valid => valid && fn && fn(deepClone(toRaw(formData.value)))); // 重置表单


    const resetFields = () => templateForm.value && templateForm.value.resetFields(); // 设置表单数据和配置


    const setFormDataAndOptions = () => {
      if (options) {
        let currentOptions = options.value,
            optionsRules = currentOptions && currentOptions.rules;

        if (currentOptions && optionsRules && Object.keys(optionsRules).length > 0) {
          Object.keys(optionsRules).forEach(key => {
            if (optionsRules[key].required && !Array.isArray(optionsRules[key]) && !optionsRules[key].message && !optionsRules[key].validator) {
              currentOptions && (currentOptions.rules[key].message = "此项必填");
            }
          });
        }

        let formRawData = { ...toRaw(formInfo.value)
        };
        Object.keys(formRawData).forEach(key => {
          // let arrayTypes = ['select'];
          // if (arrayTypes.indexOf(key) > -1 && !formRawData[key]) result[key] = []
          // else 
          formData.value[key] = typeof formRawData[key] == 'object' ? deepClone(formRawData[key]) : formRawData[key];
          /* result[key] = typeof formRawData[key] == 'object' ? deepClone(formRawData[key]) : formRawData[key]
          return result */
        }, {});
        /* formData.value = Object.keys(formRawData).reduce((result: any, key: string) => {
            // let arrayTypes = ['select'];
            // if (arrayTypes.indexOf(key) > -1 && !formRawData[key]) result[key] = []
            // else
            result[key] = typeof formRawData[key] == 'object' ? deepClone(formRawData[key]) : formRawData[key]
            return result
        }, {}) */

        formOptions.value = { ...defaultFormOptions,
          ...currentOptions
        };
      }
    }; // 更新属性值


    const changeItemValue = (value, key) => {
      formData.value[key] = value;
    };

    watch(formInfo, () => {
      setFormDataAndOptions();
    }, {
      immediate: true,
      deep: true
    });
    return {
      formData,
      formOptions,
      templateForm,
      clickCallback,
      changeItemValue,
      validate,
      resetFields
    };
  }

});